.team {
  position: relative;
  padding-bottom: 50px;

  &__title {
    @include content-padding-inline;

    font-family: "Bebas Neue", sans-serif;
    font-size: 58px;
    font-weight: 400;
    line-height: 70px;
    letter-spacing: 0.15em;
    text-align: left;
    color: #333;

    @include on-tablet {
      margin-top: 30px;
    }

    @include on-mobile {
      margin-top: 0;
    }
  }

  &__top {
    @include content-padding-inline;

    display: flex;
    flex-direction: row;
    justify-content: space-between;

    // flex-flow: wrap;

    @include on-mobile {
      flex-direction: column;
    }
  }

  &__bcgr {
    position: absolute;
    left: 0;
    margin-top: 115px;

    @include on-mobile {
      display: none;
    }
  }

  &__bcgr-mob {
    position: absolute;
    width: 100%;
    left: 0;
    right: 0;
    top: 550px;
    z-index: -1;


    @include on-desktop {
      display: none;
    }

    @include on-tablet {
      display: none;
    }
  }

  &__martin {
    position: absolute;
    z-index: 1;
    margin-top: 30px;

    @include on-tablet {
      width: 720px;
      left: 0;
    }

    @include on-mobile {
      display: none;
    }
  }

  &__photo-wr {
    @include page-grid;

    margin-top: 145px;

    @include on-mobile {
      display: flex;
      flex-direction: column-reverse;
      margin-top: 0;
    }
  }

  &__photo {
    width: 100%;
    grid-column: 5 / 12;
    cursor: pointer;

    @include hover(transform, scale(1.2));


    @include on-tablet {
      width: 340px;
      height: 255px;
      grid-column: 4 / -1;
      grid-row: 1;
      z-index: 1;
    }
  }

  &__andy {
    position: absolute;
    right: 0;
    margin-top: 700px;

    @include on-tablet {
      left: 310px;
      width: 450px;
      margin-top: 410px;
    }

    @include on-mobile {
      display: none;
    }
  }

  &__descr-wr {
    grid-column: 1 / 5;
    z-index: 2;
    margin-top: 160px;

    @include on-tablet {
      grid-column: 1 / 4;
      margin-top: 40px;
    }

    @include on-mobile {
      display: flex;
      flex-direction: column;
      margin-top: 30px;
    }
  }

  &__discription2 {
    @include on-mobile {
      margin-bottom: 270px;
    }
  }

  &__title-bot {
    @include content-padding-inline;

    position: absolute;
    top: 990px;
    z-index: 1;
    font-family: "Bebas Neue", sans-serif;
    font-size: 139px;
    font-weight: 400;
    line-height: 167px;
    letter-spacing: 0.12em;
    text-align: left;
    color: #03839E;

    @include on-tablet {
      top: 580px;
      margin-top: 50px;
      font-family: "Bebas Neue", sans-serif;
      font-size: 80px;
      font-weight: 400;
      line-height: 96px;
      letter-spacing: 0.12em;
      text-align: left;
    }

    @include on-mobile {
      font-family: "Bebas Neue", sans-serif;
      font-size: 58px;
      font-weight: 400;
      line-height: 70px;
      letter-spacing: 0.15em;
      margin-top: -70px;
      text-align: left;
    }
  }

  &__bottom-wr {
    position: relative;
    margin-top: 250px;

    @include on-tablet {
      margin-top: 50px;
    }

    @include on-mobile {
      margin-top: 110px;
    }
  }

  &__materials {
    margin-top: 350px;

    // left: 0;
    // right: 0;
    width: 100%;
    z-index: 0;
    mix-blend-mode: darken;

    @include on-tablet {
      margin-top: 150px;
    }

    @include on-mobile {
      margin-top: 0;
    }
  }

  &__desc-bot {
    position: absolute;
    top: 1120px;
    left: 850px;
    right: 100px;

    @include on-tablet {
      top: 55%;
      left: 52%;
    }

    @include on-mobile {
      display: none;
    }
  }

  &__descr-bot {
    font-family: Manrope, sans-serif;
    font-size: 20px;
    font-weight: 700;
    line-height: 30px;
    text-align: left;
    color: #4f4f4f;
  }

  &__dedsc-sec {
    @include on-tablet {
      display: none;
    }
  }

  &__point-wrapper {
    // position: relative;

    @include on-mobile {
      display: none;
    }
  }

  &__sec-1 {
    position: absolute;
    width: 10%;
    bottom: 8%;
    left: 13%;
    z-index: 3;
  }

  &__pointon-1 {
    cursor: pointer;

    @include hover(transform, scale(1.2));
  }

  &__sec-1 > div {
    transform: translate(-40%);
    opacity: 0;
  }

  &__sec-2 > div {
    transform: translateX(-37%) translateY(-134%);
    opacity: 0;
  }

  &__sec-3 > div {
    transform: translateX(-110%) translateY(-100%);
    opacity: 0;
  }

  &__sec-4 > div {
    transform: translateX(-32%) translateY(-130%);
    opacity: 0;
  }

  &__sec-5 > div {
    transform: translate(-40%);
    opacity: 0;
  }

  &__sec-1:hover > div {
    opacity: 0.4;
  }

  &__sec-2 {
    position: absolute;
    width: 10%;
    bottom: 17%;
    left: 33%;
    z-index: 3;
  }

  &__pointon-2 {
    cursor: pointer;

    @include hover(transform, scale(1.2));
  }

  &__sec-2:hover > div {
    opacity: 0.4;
  }

  &__sec-3 {
    position: absolute;
    bottom: -3%;
    left: 45%;
    z-index: 2;
  }

  &__pointon-3 {
    cursor: pointer;

    @include hover(transform, scale(1.2));
  }

  &__sec-3:hover > div {
    opacity: 0.4;
  }

  &__sec-4 {
    position: absolute;
    bottom: 5%;
    left: 55%;
    z-index: 2;
  }

  &__pointon-4 {
    cursor: pointer;

    @include hover(transform, scale(1.2));
  }

  &__sec-4:hover > div {
    opacity: 0.4;
  }

  &__sec-5 {
    position: absolute;
    width: 10%;
    bottom: 120px;
    right: 90px;
    z-index: 2;
  }

  &__pointon-5 {
    cursor: pointer;

    @include hover(transform, scale(1.2));
  }

  &__sec-5:hover > div {
    opacity: 0.4;
  }

  &__pointtext-wrapper {
    cursor:default;
    width: 200px;
    height: auto;
    padding: 35px 45px;
    background-color: #000;
    opacity: 0.4;
    border-radius: 5px;
  }

  &__point-text {
    font-family: Manrope, sans-serif;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    text-align: left;
    color: #f8f8f8;
  }
}

p {
  font-family: Manrope, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  text-align: left;
  color: #4f4f4f;
}

.romb {
  font-size: 27px;
}
